import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import './index.css';
import './short.css';

import Background from './containers/background';
import Taskbar from './components/taskbar';
import ActMenu from './components/menu';
import {
  StartMenu,
  DesktopApp,
  SidePane,
  WidPane,
  CalnWid
} from './components/start';

import * as Applications from './containers/applications';
import {EdgeMenu} from './containers/applications';

function App() {
  // extract data from the redux store state
  const apps = useSelector(state => state.apps); 
  const dispatch = useDispatch();

  const afterMath = (event)=>{
    var ess = [
      ["START","STARTHID"],
      ["PANE","PANEHIDE"],
      ["WIDG","WIDGHIDE"],
      ["CALN","CALNHIDE"],
      ["MENU","MENUHIDE"]
    ];

    try{
      var actionType = event.target.dataset.action || "";
    }catch(err){
      var actionType = "";
    }

    var actionType0 = getComputedStyle(event.target).getPropertyValue('--prefix');

    ess.forEach((item, i) => {
      if(!actionType.startsWith(item[0]) && !actionType0.startsWith(item[0])){
        dispatch({type: item[1]});
      }
    });
  }

  window.addEventListener("contextmenu", e => {
    afterMath(e);
    e.preventDefault();
    // dispatch({ type: 'GARBAGE'});
    var data = {
      top: e.clientY,
      left: e.clientX
    };

    if(e.target.dataset.menu!=null){
      data.menu = e.target.dataset.menu;
      dispatch({ type: 'MENUSHOW', payload: data});
    }

  });

  window.addEventListener("click", e => {
    // console.log(event.target);
    afterMath(e);
  });

  return (
    <div className="App">
      <Background/>
      <div className="desktop" data-menu="desk">
        <DesktopApp/>
        {Object.keys(Applications).map((key,idx)=>{
          var WinApp = Applications[key];
          return <WinApp key={idx}/>;
        })}
        <StartMenu/>
        <SidePane/>
        <WidPane/>
        <CalnWid/>
      </div>
      <Taskbar/>
      <ActMenu/>
    </div>
  );
}

export default App;
